:root {
  --main-text-color: #000;
  --secondary-text-color: #fff;
  --background-blue: #075aae;
  --logo-accent: #d3e52f;
  --button-hover-color: #067ae0;
  --offers-decorate-line: rgba(255, 255, 255, .47);
  --section-background-color: #f3f8fb;
  --footer-background-color: #075aaf;
  --card-button-hover: #f5f5f5;
  --accent-color: #2196f3;
  --primary-btn-hover-color: #188ce8;
  --background-color: #fff;
  --footer-contacts-color: rgba(255, 255, 255, .6);
  --button-background-color: #f5f4fa;
  --gallery-border-color: #eee;
  --hero-gradient: rgba(47, 48, 58, .4);
  --social-color: #afb1b8;
  --footer-social: rgba(255, 255, 255, .1);
  --specialization-bkg: rgba(47, 48, 58, .8);
  --backdrop-color: rgba(0, 0, 0, .2);
  --timing-function: cubic-bezier(.4, 0, .2, 1);
  --galery-overlay: rgba(33, 150, 243, .9);
  --modal-form-border-color: rgba(33, 33, 33, .2);
  --input-border-active-color: #2196f3;
}

h1, h2, h3, h4, p {
