body {
  color: var(--main-text-color);
  background-color: var(--background-color);
  letter-spacing: .03em;
  text-align: left;
  font-family: Montserrat, sans-serif;
  font-size: 14px;
  line-height: 1.71;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.list, .services, .team, .offers, .menu, .social {
  margin: 0;
  padding: 0;
  list-style: none;
}

.link, .menu__link, .logo, .button-link {
  color: inherit;
  margin: 0;
  padding: 15px 0;
  text-decoration: none;
  display: inline-block;
}

.container, .header__container {
  width: 480px;
  margin: 0 auto;
  padding: 0 15px;
}

@media screen and (max-width: 480px) {
  .container, .header__container {
    width: 100%;
  }
}

@media screen and (min-width: 768px) {
  .container, .header__container {
    width: 768px;
  }
}

@media screen and (min-width: 1200px) {
  .container, .header__container {
    width: 1200px;
  }
}

.section {
  background-color: var(--background-color);
  padding-top: 45px;
  padding-bottom: 45px;
}

@media screen and (min-width: 1200px) {
  .section {
    padding-top: 60px;
    padding-bottom: 60px;
  }
}

.section-text {
  color: var(--main-text-color);
  margin-bottom: 45px;
}

@media screen and (min-width: 1200px) {
  .section-text {
    margin-bottom: 45px;
  }
}

.section-text--white {
  color: var(--secondary-text-color);
}

.section-title {
  color: var(--primary-text-color);
  text-align: left;
  letter-spacing: .03em;
  margin-top: 0;
  margin-bottom: 20px;
  font-size: 25px;
  font-weight: 400;
  line-height: 1;
}

@media screen and (min-width: 1200px) {
  .section-title {
    font-size: 36px;
    line-height: setLineHeight(42px, 36px);
    margin-bottom: 45px;
  }
}

.gallery_item {
  width: 100%;
  height: 600px;
}

@keyframes socialAnimation {
  0% {
    transform: scale(0);
  }

  100% {
    transform: scale(1);
  }
}

.button-link {
  min-width: 160px;
  min-height: 50px;
  background-color: var(--background-blue);
  color: var(--secondary-text-color);
  text-align: center;
  transition: color 250ms var(--timing-function), background-color 250ms var(--timing-function), box-shadow 250ms var(--timing-function);
  padding: 15px 10px;
}

.button-link + .button-link {
  margin-left: 10px;
}

.button-link:hover, .button-link:focus {
  background-color: var(--button-hover-color);
  color: var(--secondary-text-color);
  box-shadow: 0 4px 4px rgba(0, 0, 0, .15);
}

.button-link--white {
  background-color: var(--secondary-text-color);
  color: var(--primary-text-color);
}

.logo {
  letter-spacing: .03em;
  align-items: center;
  padding: 16px 0;
  font-size: 24px;
  line-height: 1.16667;
  display: inline-flex;
}

@media screen and (min-width: 1200px) {
  .logo {
    padding: 14px 0;
    font-size: 26px;
    line-height: 1.19231;
  }
}

.logo--footer {
