    margin-left: 0;
  }
}

.menu__link {
  color: var(--primary-text-color);
  transition: color 250ms var(--timing-function);
  padding: 32px 0;
}

.menu--container.is-open .menu__link {
  width: 100%;
  display: block;
}

.menu__link:hover, .menu__link:focus, .current {
  color: var(--accent-color);
}

@media screen and (min-width: 768px) {
  .current:after {
    content: "";
    background-color: var(--accent-color);
    width: 100%;
    height: 4px;
    border-radius: 2px;
    position: absolute;
    bottom: -1px;
    left: 0;
  }
}

.hamburger {
  z-index: 30;
  color: var(--primary-text-color);
  transition: color 250ms var(--timing-function);
  background-color: rgba(0, 0, 0, 0);
  border: 0;
  margin: 0;
  padding: 0;
  display: inline-flex;
  position: absolute;
  top: 50%;
  right: 15px;
  transform: translateY(-50%);
}

.hamburger:hover, .hamburger:focus {
  color: var(--accent-color);
}

.hamburger .hamburger__icon-close, .hamburger.is-open .hamburger__icon-menu {
  opacity: 0;
}

.hamburger.is-open .hamburger__icon-close {
  opacity: 1;
}

@media screen and (min-width: 768px) {
  .hamburger {
    display: none;
  }
}

.hamburger__icon {
  fill: currentColor;
}

.hamburger__icon-close {
  opacity: 0;
  transition: opacity 250ms var(--timing-function);
}

.hamburger__icon-menu {
  opacity: 1;
  transition: opacity 250ms var(--timing-function);
}

