  transform: rotate(-90deg);
}

.team__item[data-card-active="inactive"]:after {
  opacity: .6;
  transform: translate3d(10px, 10px, 0);
}

.team__item[data-card-active="active"]:after {
  opacity: 1;
  transform: translate3d(16px, 40px, 0);
}

.team__item[data-card-active="active"] > .team__social {
  height: 40px;
  visibility: visible;
}

.team__item[data-card-active="inactive"] > .team__social {
  height: 0;
  visibility: hidden;
}

.team__item[data-card-active="active"] > .team__social > .team__social-link:nth-child(1) {
  animation: socialAnimation .15s linear .65s forwards;
}

.team__item[data-card-active="inactive"] > .team__social > .team__social-link:nth-child(1) {
  transform: scale(0);
