.team__item[data-card-active="active"] > .team__social > .team__social-link:nth-child(2) {
  animation: socialAnimation .15s linear .5s forwards;
}

.team__item[data-card-active="inactive"] > .team__social > .team__social-link:nth-child(2) {
  transform: scale(0);
}

